.social-list {
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
  gap: 15px;
  min-height: 62px;
  border-bottom: 1px solid #d9d9d9;

  @media (min-width: @tablet-width) {
    border: none;
  }
}

.social-list__item {
  color: @dark-grey2;

  @media (min-width: @tablet-width) {
    gap: 26px;
  }
}
