.main-header {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  min-height: 65px;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    width: 708px;
  }

  @media (min-width: @desktop-width) {
    width: 1220px;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 50px;

    &--index {
      background-image: url("../img/desktop/index-background.png");
      background-repeat: no-repeat;
      background-position: right;
    }
  }
}

.main-header__logo {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 280px;
  min-height: 65px;

  @media (min-width: @tablet-width) {
    width: 174px;
  }
}

.main-header__logo-link {
  display: flex;
  align-items: center;
}

.main-header__logo-image {
  margin-right: 56.66px;

  @media (min-width: @tablet-width) {
    margin-right: 12px;
  }
}

.main-header__logo-button {
  background-color: @white;
  border: none;

  @media (min-width: @tablet-width) {
    display: none;
  }
}
